




















import {
  Component,
  Vue,
  Prop
} from 'vue-property-decorator'
import coupon from "@/components/couponCom/coupon/coupon.vue"
interface fatherType extends Vue {
   showType?:boolean
}
import {ApiCouponType} from "@/components/couponCom/coupon/couponType"
@Component({
  components: {
    coupon
  }
})
export default class Pop extends Vue {
  @Prop()
  showType: boolean
  @Prop()
  couponList:Array<ApiCouponType>=[]
  get list() {
    return [].slice.call(this.couponList)
  }

  onClose() {
    this.$STORE.setStore.setTabVisible(true)
    const parent:fatherType = this.$parent
    parent.showType = false
  }
  get showTypeProp() {
    return this.showType
  }

  myCouponGet(e) {
    this.$emit("myCouponGet", e);
  }
}
